import React, { Component } from "react";
import { connect } from "react-redux";

class NFLleaders extends Component {
  render() {
    return (
      <div>
        <h1>NFL leaders</h1>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    page: state.page
  };
};

export default connect(mapStateToProps)(NFLleaders);
