const page = (page = ["NEWS", "NHL", "NBA", "NFL", "MLB"], action) => {
  switch (action.type) {
    case "NHL":
      return ["NHL", "SCORES", "NEWS", "STANDINGS", "LEADERS"];
    case "NBA":
      return ["NBA", "SCORES", "NEWS", "STANDINGS", "LEADERS"];
    case "NFL":
      return ["NFL", "SCORES", "NEWS", "STANDINGS", "LEADERS"];
    case "MLB":
      return ["MLB", "SCORES", "NEWS", "STANDINGS", "LEADERS"];
    case "home":
      return ["NEWS", "NHL", "NBA", "NFL", "MLB"];
    default:
      return page;
  }
};

export default page;
