export const fetchTopNews = () => {
  return async dispatch => {
    dispatch({
      type: "FETCH_TOP_NEWS_REQUEST"
    });
    const KEY = "8b3628b3ee564cf4b7d4ab2d52a584f1";
    const info = await fetch(
      `https://newsapi.org/v2/top-headlines?language=en&country=ca&category=sports&apiKey=${KEY}`
    );
    const news = await info.json();
    dispatch({
      type: "FETCH_TOP_NEWS",
      payload: news
    });
  };
};
