import React from "react";
import { connect } from "react-redux";
import { fetchTopNews } from "../actions/index";
import NewsCard from "./NewsCard";
import moment from "moment";
import MainNews from "./MainNews";

class Home extends React.Component {
  componentDidMount() {
    this.props.fetchTopNews();
  }

  render() {
    if (this.props.loading) {
      return (
        <div>
          <h1>LOADING</h1>
        </div>
      );
    } else if (this.props.topNews.articles) {
      const author = this.props.topNews.articles.map(
        element => element.source.name
      );

      const title = this.props.topNews.articles.map(
        element => element.title.split("-")[0]
      );

      const time = this.props.topNews.articles.map(
        element => element.publishedAt
      );

      const timestamp = time.map(element => moment(element).fromNow());

      const newsImage = this.props.topNews.articles.map(
        element => element.urlToImage
      );

      return (
        <div className="home-container">
          <div className="box-container">
            <div className="main-news">
              <MainNews
                topNews={this.props.topNews}
                author={author[0]}
                title={title[0]}
                time={timestamp[0]}
                newsImage={newsImage[0]}
              />
            </div>
            <div className="news-card">
              <NewsCard
                topNews={this.props.topNews}
                author={author[1]}
                title={title[1]}
                time={timestamp[1]}
                newsImage={newsImage[1]}
              />
            </div>
            <div className="news-card">
              <NewsCard
                topNews={this.props.topNews}
                author={author[2]}
                title={title[2]}
                time={timestamp[2]}
                newsImage={newsImage[2]}
              />
            </div>
            <div className="news-card">
              <NewsCard
                topNews={this.props.topNews}
                author={author[3]}
                title={title[3]}
                time={timestamp[3]}
                newsImage={newsImage[3]}
              />
            </div>
            <div className="news-card">
              <NewsCard
                topNews={this.props.topNews}
                author={author[4]}
                title={title[4]}
                time={timestamp[4]}
                newsImage={newsImage[4]}
              />
            </div>
            <div className="news-card">
              <NewsCard
                topNews={this.props.topNews}
                author={author[5]}
                title={title[5]}
                time={timestamp[5]}
                newsImage={newsImage[5]}
              />
            </div>
            <div className="news-card">
              <NewsCard
                topNews={this.props.topNews}
                author={author[6]}
                title={title[6]}
                time={timestamp[6]}
                newsImage={newsImage[6]}
              />
            </div>
            <div className="news-card">
              <NewsCard
                topNews={this.props.topNews}
                author={author[7]}
                title={title[7]}
                time={timestamp[7]}
                newsImage={newsImage[7]}
              />
            </div>
            <div className="news-card">
              <NewsCard
                topNews={this.props.topNews}
                author={author[8]}
                title={title[8]}
                time={timestamp[8]}
                newsImage={newsImage[8]}
              />
            </div>
            <div className="news-card">
              <NewsCard
                topNews={this.props.topNews}
                author={author[9]}
                title={title[9]}
                time={timestamp[9]}
                newsImage={newsImage[9]}
              />
            </div>
          </div>
        </div>
      );
    }
    return <div></div>;
  }
}

const mapStateToProps = state => {
  return {
    topNews: state.topNews.news,
    loading: state.topNews.loading
  };
};

const mapDispatchToProps = () => {
  return {
    fetchTopNews
  };
};

export default connect(mapStateToProps, mapDispatchToProps())(Home);
